        bastBanner:[],
        cursorI:"",
        goodsList:[]
    }
  },
  methods:{
    // 八宫格跳转
    toa(ids){
      switch(ids){
        case 0:
          // 分类
          // 1.字符串
          this.$router.push("/sort");
          // 2.对象
          // this.$router.push({ path: '/sort' })
          // 3.命名的路由
