          // 优惠劵
          // this.$router.push("/coupons");
          break;
        case 2:
          break;
        case 3:
          break;
        case 4:
          // 收藏
          this.$router.push("/collection");
          break;
        case 5:
          break;
        case 6:
          // 地址
          this.$router.push("/address");
          break;
        case 7:
          // 订单
          this.$router.push("/order");
          break;
      }
    },
    swChange(index){
      this.cursorI= index;
    },
  },
  created(){
     // 轮播图片
     this.axios.get("http://47.115.51.185/api/index").then(res=>{
              console.log(res.data.data);
              console.log(res.data.data.info);
              // 轮播图
              this.bannerArr = res.data.data.banner;
              // 八宫格
              this.menus = res.data.data.menus;
              // 滚动信息
              this.newsRoll = res.data.data.roll;
              // 快速选择
              this.fastList = res.data.data.info.fastList;
              // 精选轮播图
              this.bastBanner = res.data.data.info.bastBanner;
              // 商品列表
              this.goodsList = res.data.data.info.bastList;

              // console.log(this.fastList.cart_name);
      })
      }
}
